:root {
  --main-color: #2f4f4f;
  --number-color: #8b0000;
  --font-size: 20px;
  --bcg-color: #eee;
  --color-sport-pay: #000;
}

* {
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: var(--font-size);
  color: var(--main-color);
  margin: 0;
  font-weight: bold;
}

img {
  width: 100%;
}

.people {
  margin: 30px 35px 155px;
}

.person {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 231px;
  transition: all 1s;
  display: flex;
  position: relative;
}

.person:hover {
  width: 220px;
  height: 287px;
}

.person:hover:after {
  content: "";
  background-color: var(--bcg-color);
  z-index: -1;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.photo {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  position: relative;
  overflow: hidden;
}

.photo:after {
  content: "";
  color: var(--number-color);
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  line-height: 32px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.sport, .pay {
  color: var(--color-sport-pay);
  font-weight: normal;
  display: none;
}

.details {
  margin-top: 8px;
}

.people {
  grid-template-columns: repeat(2, auto);
  justify-content: center;
  gap: 40px 30px;
  display: grid;
}

.person:hover .sport, .person:hover .pay {
  display: inline;
}

@media (width >= 500px) {
  .people {
    grid-template-columns: repeat(2, auto);
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, auto);
  }
}

.person:first-child .photo:after {
  content: "1";
}

.person:nth-child(2) .photo:after {
  content: "2";
}

.person:nth-child(3) .photo:after {
  content: "3";
}

.person:nth-child(4) .photo:after {
  content: "4";
}

.person:nth-child(5) .photo:after {
  content: "5";
}

.person:nth-child(6) .photo:after {
  content: "6";
}

.person:nth-child(7) .photo:after {
  content: "7";
}

.person:nth-child(8) .photo:after {
  content: "8";
}

.person:nth-child(9) .photo:after {
  content: "9";
}

.person:nth-child(10) .photo:after {
  content: "10";
}
